header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  composes: container from '../../index.css';

  height: 100%;
  text-align: center;
  position: relative;
}

header h1 {
  color: var(--color-primary);
}
/* ******************* CTA ****************** */
.cta {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  gap: 1.2rem;
}

/* ******************* HEADER SOCIALS ****************** */
.header__socials {
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  position: absolute;
  left: 1.5rem;
  top: 50%;
  transform: translateY(-50%);
}

.header__socials::after {
  content: '';
  width: 2px;
  height: 3rem;
  background-color: var(--color-primary);
  margin-left: .75rem; /*half of the header socials left side*/
  cursor: pointer;
}

/* ******************* ME ****************** */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 5rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ******************* HEADER CHILLOUT ****************** */
.header__chillout {
  font-weight: 300;
  font-size: .9rem;
  position: absolute;
  right: 1.5rem;
  top: 50%;
  transform: translateY(-50%) rotate(90deg);
}


/* *********** Media (Medium Devices) *********** */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}


/* *********** Media (Small Devices) *********** */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials, .header__chillout {
    display: none;
  }
}


.btn {
  width: fit-content;
  display: inline-block;
  padding: 0.75rem 1.2rem;
  transition: var(--transition);
  border: 1px solid var(--color-primary);
  border-radius: 0.4rem;
  cursor: pointer;
  color: var(--color-primary);
}
.btn:hover {
  background-color: var(--color-white);
  border-color: transparent;
  color: var(--color-bg);
}
.btn-primary {
  background-color: var(--color-primary);
  color: var(--color-bg)
}